import { watch, onBeforeMount, onUnmounted, onDeactivated, onActivated, getCurrentInstance } from 'vue';
import context from '.';
export function resolveLock() {
  var lockCounts = Object.keys(context.locks).length;
  lockCounts <= 0 ? document.body.classList.remove('var--lock') : document.body.classList.add('var--lock');
}
export function addLock(uid) {
  context.locks[uid] = 1;
  resolveLock();
}
export function releaseLock(uid) {
  delete context.locks[uid];
  resolveLock();
}
/**
 * 
 * @param props props
 * @param state propskey
 * @param use propskey
 */

export function useLock(props, state, use) {
  var {
    uid
  } = getCurrentInstance();

  if (use) {
    watch(() => props[use], newValue => {
      if (newValue === false) {
        //  
        releaseLock(uid);
      } else if (newValue === true && props[state] === true) {
        //  popup 
        addLock(uid);
      }
    });
  }

  watch(() => props[state], newValue => {
    if (use && props[use] === false) {
      return;
    }

    if (newValue === true) {
      // popup 
      addLock(uid);
    } else {
      // popup 
      releaseLock(uid);
    }
  });
  onBeforeMount(() => {
    if (use && props[use] === false) {
      return;
    }

    if (props[state] === true) {
      // popup  
      addLock(uid);
    }
  });
  onUnmounted(() => {
    if (use && props[use] === false) {
      return;
    }

    if (props[state] === true) {
      // popup  
      releaseLock(uid);
    }
  });
  onActivated(() => {
    if (use && props[use] === false) {
      return;
    }

    if (props[state] === true) {
      // popup keepalive 
      addLock(uid);
    }
  });
  onDeactivated(() => {
    if (use && props[use] === false) {
      return;
    }

    if (props[state] === true) {
      // popup keepalive 
      releaseLock(uid);
    }
  });
}