function modeValidator(mode) {
  return ['linear', 'circle'].includes(mode);
}

export var props = {
  // progress 
  mode: {
    type: String,
    default: 'linear',
    validator: modeValidator
  },
  // progress 
  lineWidth: {
    type: [Number, String],
    default: 4
  },
  // progress 
  color: {
    type: String
  },
  // progress 
  trackColor: {
    type: String
  },
  // 
  ripple: {
    type: Boolean,
    default: false
  },
  // progress 
  value: {
    type: [Number, String],
    default: 0
  },
  // label
  label: {
    type: Boolean,
    default: false
  },
  // circle 
  size: {
    type: Number,
    default: 40
  },
  // circle
  rotate: {
    type: Number,
    default: 0
  },
  //  circle 
  track: {
    type: Boolean,
    default: true
  }
};